@import "../../styles/Colors.scss";

.create-specialty-dialog-wrapper {
	.create-specialty-dialog {
		flex: 1;
		padding: 1.5rem 1rem;

		& > *:not(:last-child) {
			margin-bottom: 1rem;
		}

		.create-specialty-dialog {
			&__title {
				font-family: "Montserrat", "Roboto", sans-serif;
				font-weight: 500;
				font-size: 1.25rem;
				padding: 0;
				margin-bottom: 0.5rem;
			}

			&__btn {
				background-color: $blue;
				margin-top: 1rem;
			}
		}
	}
}
